import React, { useEffect, useState } from 'react'
import { CRow } from '@coreui/react'
import { Container } from 'react-bootstrap'
import axios from 'axios'
import { message } from 'antd'

const WidgetsDropdown = () => {
  const [time, setTime] = useState()
  const [numberOfPatient, setNumberOfPatient] = useState(0)

  const currentTime = () => {
    const date = new Date()
    const hours = date.getHours()
    const minutes = date.getMinutes()
    const seconds = date.getSeconds()
    return `${hours}:${minutes}:${seconds}`
  }
  //eslint-disable-next-line
  const getNumberOfPatientVisitedToday = async () => {
    try {
      const res = await axios.get('/api/v1/receptionist/getNumberOfPatientVisitedToday')
      if (res.data.success) {
        setNumberOfPatient(res.data.data)
      }
    } catch (error) {
      console.log(error)
      message.error(error.response.data.message)
    }
  }

  useEffect(() => {
    setInterval(() => {
      setTime(currentTime())
    }, 1000)
  }, [])

  useEffect(() => {
    getNumberOfPatientVisitedToday()
  }, [])
  return (
    <CRow className="mb-3 h-100">
      <div className="col-12 col-md-12 col-lg-4 mb-3 mb-lg-0">
        <Container className="bg-black h-100 rounded-2 d-flex flex-column w-100 align-items-center justify-content-center">
          <Container className="fs-3 text-white">
            Visits<span className="text-warning"> {numberOfPatient}</span>
          </Container>
          <Container className="text-success fs-3">{time}</Container>
        </Container>
      </div>

      <div className="col-lg-8">
        <Container className="w-100 bg-warning rounded-2 h-100 col-5" fluid>
          <div className="h-100">
            <div className="">
              <h3>Alerts</h3>
            </div>
            <div>
              <marquee>
                {' '}
                <h4>Dont be safety blinded be safety minded</h4>
              </marquee>
            </div>
          </div>
        </Container>
        {/* <CWidgetStatsA
          className="mb-4 h-100 w-100 "
          color="warning"
          value={
            <>
              Alerts <CIcon icon={cilBell} color="light" size="lg" />
              <marquee>hello world</marquee>
            </>
          }
        /> */}
      </div>
    </CRow>
  )
}

export default WidgetsDropdown
