import React, { useEffect, useState } from 'react'
import { Card, Container } from 'react-bootstrap'
import WidgetsDropdown from '../widgets/WidgetsDropdown'
import { Divider, message } from 'antd'
import Badge from 'react-bootstrap/Badge'
import axios from 'axios'

const Index = () => {
  const [recent4Patient, setRecent4Patient] = useState([])

  //eslint-disable-next-line
  const getrecent4Patient = async () => {
    try {
      const res = await axios.get('/api/v1/receptionist/getrecent4Patient')
      if (res.data.success) {
        setRecent4Patient(res.data.data)
      } else {
        message.error(res.data.message)
      }
    } catch (error) {
      console.log(error)
      message.error(error.message)
    }
  }
  useEffect(() => {
    getrecent4Patient()
  }, [])
  return (
    <>
      <Container className="mb-3">
        <WidgetsDropdown />
        <Card title="Recent Patients" className="shadow-sm">
          <Card.Header>Recent Patients</Card.Header>
          <Container className="mt-2">
            {recent4Patient.length > 0 ? (
              recent4Patient.map((patient, idx) => (
                <>
                  <Card className="border-0 mx-3" key={idx}>
                    <div>{patient.uniqueId}</div>
                    <div>{patient.department}</div>
                    <div className="d-flex justify-content-between">
                      <div>{patient.patientName}</div>
                      <div></div>
                      <div className="">
                        {patient.isChecked ? (
                          <Badge bg="success" pill>
                            Checked
                          </Badge>
                        ) : (
                          <Badge bg="warning" pill>
                            Pending
                          </Badge>
                        )}
                      </div>
                    </div>
                  </Card>
                  <Divider className="my-2" />
                </>
              ))
            ) : (
              <div className="d-flex p-5 justify-content-center text-danger">
                No Recent Patients
              </div>
            )}
          </Container>
        </Card>
      </Container>
    </>
  )
}

export default Index
